.header_wrapper{--search-height: 46px;--search-top-spacer: 9px;--search-button-width: 52px;--search-border-radius: 25px;width:100%;height:60px;display:flex;justify-content:space-between;align-items:center;box-shadow:1px 0 10px var(--border-primary);position:sticky;background-color:var(--primary-bg);right:0;top:0;z-index:100}.switch-mode{position:relative;display:inline-block;width:60px;height:34px;border-radius:18px;overflow:hidden}.switch-checkbox{opacity:0;width:0;height:0}.switch-checkbox:checked+.change_state_mode{background-color:#2196f3}.switch-checkbox:checked+.change_state_mode:before{content:"ðŸŒ‘";text-align:center;line-height:26px;font-size:2rem;transform:translateX(26px)}.change_state_mode{position:absolute;cursor:pointer;top:0;left:0;right:0;bottom:0;background-color:#ccc;transition:background-color .5s ease-in-out}.change_state_mode::before{content:"â˜€";color:#ff0;text-align:center;line-height:26px;font-size:3rem;position:absolute;height:26px;width:26px;border-radius:50%;left:4px;bottom:4px;background-color:#fff;transition:.5s ease-in-out}.logo_box{width:25%;height:100%;padding:0 20px;display:flex;align-items:center}.logo_box img{width:50px;height:50px;border-radius:10px}.logo_box span{display:inline-flex;padding:0 10px;align-items:center}.logo_box span h2{color:var(--text-color);font-size:1.7rem}.logo_navigate{align-items:center}.action_box{width:30%;display:flex;align-items:center;justify-content:space-around}.account{display:flex;align-items:center;justify-content:space-around;gap:10px;transition:background-color .3s ease-in-out;border-radius:25px;padding:5px}.account img{width:40px;height:40px;border-radius:50%}.account p{font-size:1.4rem;font-weight:600;color:var(--text-color)}.account:hover{background-color:var(--bg-content-color)}